import React, { useContext, useEffect, useRef, useState } from 'react'
import axios from 'axiosbase'
import getLoggedUserToken from 'components/utilities/getLoggedUserToken'
import AlertContext from 'components/utilities/alertContext'
import { DataGrid, TabPanel } from 'devextreme-react'
import { Column, FilterRow, HeaderFilter, MasterDetail, Pager, Paging, Scrolling, SearchPanel } from 'devextreme-react/data-grid'
import { HeaderCell } from './trainingsList'
import { Item } from 'devextreme-react/tab-panel'
import { RenderRichText } from './registeredUsersList'
import './style.css'

export default function TrainingRequestsList() {
   const [requests, setRequests] = useState([])
   const [sectors, setSectors] = useState([])

   const AlertCtx = useContext(AlertContext)

   useEffect(() => {
      axios.get('trainings/requests', {
         headers: { Authorization: getLoggedUserToken() },
      }).then(res => {
         console.log(res)
         setRequests(res.data.data)
      }).catch(err => {
         console.log(err.response)
         AlertCtx.setAlert({ open: true, variant: 'error', msg: ' .     .' })
      })

      axios.get('business-sector').then(res => {
         console.log(res.data)
         setSectors(res.data.data)
      })
   }, [])

   const calculateCellSector = (rowdata) => {
      const sectorId = rowdata.business_sectorId
      return sectors.find(sector => sector.id === sectorId)?.bdescription_mon
   }

   const containerRef = useRef()

   const [width, setWidth] = useState()

   useEffect(() => {
      const handleResize = () => {
         setWidth(containerRef.current?.clientWidth)
         console.log(containerRef.current?.clientWidth)
      }
      if (width === undefined) handleResize()
      window.addEventListener('resize', handleResize)

      return () => window.removeEventListener('resize', handleResize)
   }, [])

   return (
      <div className="tw-rounded tw-shadow-md tw-bg-white tw-w-full tw-p-2 tw-pb-14" ref={containerRef}>
         <div className="tw-text-center tw-p-2 tw-mt-6 tw-text-lg tw-font-semibold">
              
         </div>

         <DataGrid
            id="training-requests-datagrid"
            dataSource={requests}
            showBorders={true}
            wordWrapEnabled={true}
            rowAlternationEnabled={true}
            columnAutoWidth={true}
            showRowLines={true}
            showColumnLines={true}
            loadPanel={{ enabled: true, height: 300, text: ' ' }}
            noDataText="  ."
            width={width && width - 25}
         >
            <SearchPanel visible={true} width={240} placeholder="..." />
            <Scrolling mode="standard" columnRenderingMode="standard" showScrollbar="always" />
            <Paging defaultPageSize={40} />
            <Pager showPageSizeSelector={true} allowedPageSizes={[10, 20, 40]} showInfo={false} showNavigationButtons={true} />
            <HeaderFilter visible={true} />
            <FilterRow visible={true} />

            <Column dataField="fullname" caption=" " headerCellRender={HeaderCell} minWidth={160} />
            <Column dataField="employee_position" caption="   " headerCellRender={HeaderCell} minWidth={160} />
            <Column dataField="registration_number" caption=" " headerCellRender={HeaderCell} />
            <Column dataField="phone" caption=" " headerCellRender={HeaderCell} />
            <Column dataField="company_email" caption=" " headerCellRender={HeaderCell} />
            <Column dataField="business_sectorId" caption=" " calculateCellValue={calculateCellSector} headerCellRender={HeaderCell} minWidth={240} />
            <Column dataField="company_registration_number" caption="  " headerCellRender={HeaderCell} />
            <Column dataField="participant_number" caption="   " headerCellRender={HeaderCell} minWidth={100} />

            <MasterDetail enabled={true} component={MasterDetails} />
         </DataGrid>
      </div>
   )
}

const MasterDetails = ({ data }) => (
   <TabPanel>
      <Item title="   " render={() => <RenderRichText html={data.data.company_introduction} />} />
      <Item title=" ,  " render={() => <RenderRichText html={data.data.training_request} />} />
      <Item title="  " render={() => <RenderRichText html={data.data.training_benefit} />} />
   </TabPanel>
)
